import Grid from '@mui/material/Grid';
import Pad from '../pad/Pad';
import Box from '@mui/system/Box/Box';

import padsData from '../pad/pads.data';
import { useAppSelector } from '../../redux/hooks';
import { drumStateSelection } from './drumSlice';
import Typography from '@mui/material/Typography';

const Drum = () => {
  const drumState = useAppSelector(drumStateSelection);

  return (
    <Grid id='drum-machine' sx={{ margin: '0 auto', width: '100vw', textAlign: 'center' }}>
      <Grid container>
        {padsData.map((padData, index) => {
          return (
            <Grid key={padData.key} item sm={4}>
              <Pad padData={padData}></Pad>
            </Grid>
          );
        })}
      </Grid>

      <Box id='display'>
        <h1>{drumState.display}</h1>
        <Typography variant='h6' color='primary'>
          {drumState.display}
        </Typography>
      </Box>
    </Grid>
  );
};

export default Drum;
